<template>
    <div>
        <canvas :id="circleId" width="150px" height="150px">
            浏览器或者次版本不支持canvas
        </canvas>
        <audio :ref="'audio'+circleId">
            <source src="./../assets/audio/konglong.mp3" />
        </audio>
    </div>
</template>

<script>
export default {
    props: {
        circleId: {
            type: String,
            default: '',
        },
        circleNum: {
            type: Number,
            default: 0
        },
        circleStyle: {
            type: Object,
            default: () => {
                return {
                    'circleColor': '#FBEC99',
                    'textColor': '#F6B71E'
                }
            }
        }
    },
    data() {
        return {
            circle: null,
        };
    },
    watch: {
        circleNum: {
            handler(newVal, oldval) {
                this.cricle.setProgress(newVal);
                this.$refs['audio'+this.circleId].pause();
                this.$refs['audio'+this.circleId].play();

            },
            immediate: false,
            deep: true,
        },
    },
    computed: {},
    methods: {
        setCircle() {
            //线的宽度
            let mBorderwidth = 4;
            //波长
            let mWaveLength = 50;
            //波高度
            let mWaveheight = 8;
            let mWaveDx = 0;
            //当前进度
            let mProgress = 0;
            //最大值
            let mMaxValue = 100;
            let rem = "rem";
            let px = "px";
            //圆的颜色
            let mCircleColor = this.circleStyle.circleColor;
            //波浪的颜色
            let mWaveColor =  this.circleStyle.circleColor;
            let mTextSize = 30;
            let mTextColor =  this.circleStyle.textColor;
            let ctx;
            let width = 0;
            let height = 0;
            //波浪移动速度
            let mWaveSpeed = 2;
            //参数传入canvas的id

            function WaveCircleProgress(id) {
                let myCanvas = document.getElementById(id);
                let ctx = myCanvas.getContext("2d");
                if (!ctx) {
                    return null;
                }
                this.ctx = ctx;
                this.mProgress = mProgress;
                this.mMaxValue = mMaxValue;
                this.mCircleColor = mCircleColor;
                this.mBorderwidth = mBorderwidth;
                this.mWaveColor = mWaveColor;
                this.mTextColor = mTextColor;
                this.mTextSize = mTextSize;
                this.mWaveLength = mWaveLength;
                this.mWaveheight = mWaveheight;
                this.width = parseInt(myCanvas.getAttribute("width").replace(px, "").replace(rem, ""));
                this.height = parseInt(myCanvas.getAttribute("height").replace(px, "").replace(rem, ""));
                this.setProgress = function (progress) {
                    if (progress) {
                        if (this.chechkNum(progress)) {
                            if (progress <= 0) {
                                progress = 0;
                            }
                            if (progress >= mMaxValue) {
                                progress = mMaxValue;
                            }
                            this.mProgress = progress;
                            this.isCancel = true;
                            draw(this);
                        } else {
                            throw "the progress is not Number";
                        }
                    };
                }
                this.getProgress = function () {
                    return this.mProgress;
                };
                this.setWaveColor = function (color) {
                    if (color) {
                        this.mWaveColor = color;
                        draw(this);
                    }
                };
                this.getWaveColor = function () {
                    return this.mWaveColor;
                };
                this.setTextSize = function (size) {
                    if (size) {
                        this.mTextSize = size;
                        draw(this);
                    }
                };
                this.getTextSize = function () {
                    return this.mTextSize;
                };

                this.setTextColor = function (color) {
                    if (color) {
                        this.mTextColor = color;
                        draw(this);
                    }
                };
                this.getTextColor = function () {
                    return this.mTextColor;
                };

                this.setCircleColor = function (color) {
                    if (color) {
                        this.mCircleColor = color;
                        draw(this);
                    }
                };
                this.getCircleColor = function () {
                    return this.mCircleColor;
                };
                draw(this);
                return this;
            }



            function draw(obj) {
                drawCircle(obj);
                drawWave(obj);
                drawText(obj);
            }

            //画圆
            function drawCircle(obj) {
                if (!obj.width) {
                    w = 150;
                }
                if (!obj.height) {
                    h = 150;
                }
                let c = obj.ctx;
                if (c) {
                    let X = obj.width / 2;
                    let Y = obj.height / 2;
                    let radius = ((obj.width >= obj.height) ? (obj.height / 2) : obj.width / 2) - 2 * obj.mBorderwidth;
                    c.strokeStyle = obj.mCircleColor;
                    c.lineWidth = obj.mBorderwidth;
                    c.beginPath();
                    c.arc(X, Y, radius, 0, Math.PI * 2, true);
                    c.closePath();
                    c.stroke();
                    c.clip();
                }
            }
            //画波浪
            function drawWave(obj) {
                if (!obj.width) {
                    w = 150;
                }
                if (!obj.height) {
                    h = 150;
                }
                let mWaveDy;
                let c = obj.ctx;
                let h = obj.height;

                let w = obj.width;
                let pro = obj.mProgress;

                if (c) {
                    let mWaveHalfLength = obj.mwal / 2;
                    c.fillStyle = obj.mWaveColor;
                    c.save();
                    c.beginPath();
                    if (pro == 0) {
                        mWaveDy = h;
                    } else if (pro == obj.mMaxValue) {
                        mWaveDy = 0;
                    } else if (pro <= obj.mBorderwidth) {
                        //进度小于线宽 圆的线过宽会遮住波浪 所以上移一点
                        mWaveDy = h - 2 * obj.mBorderwidth;
                    } else if (pro >= obj.mMaxValue - obj.mBorderwidth) {
                        //避免遮住 下移一点
                        mWaveDy = h - (pro / obj.mMaxValue) * h + 2 * obj.mBorderwidth;
                    } else {
                        mWaveDy = h - (pro / obj.mMaxValue) * h;
                    }
                    //画曲线
                    c.moveTo(mWaveDx - 2 * mWaveLength, mWaveDy);
                    for (let j = -2; j <= 4; j++) {
                        let y = 0;
                        if (j % 2 == 0) {
                            y = mWaveDy + mWaveheight;
                        } else {
                            y = mWaveDy - mWaveheight;
                        }
                        c.quadraticCurveTo(mWaveDx + mWaveLength * (2 * j - 1) / 2, y, mWaveDx + mWaveLength * j, mWaveDy);
                    }
                    c.lineTo(w, h);
                    c.lineTo(0, h);
                    c.closePath();
                    c.fill();
                    c.restore();
                    if (window.requestAnimationFrame) {
                        if (obj.anim) {
                            //清楚重复
                            cancelAnimationFrame(obj.anim);
                        }
                        let id = window.requestAnimationFrame(function () {
                            //波浪移动
                            mWaveDx += mWaveSpeed;
                            if (mWaveDx >= 2 * mWaveLength) {
                                mWaveDx = 0;
                            }
                            c.clearRect(0, 0, w, h);
                            draw(obj);
                        });
                        obj.anim = id;
                    } else {
                        if (obj.anim) {
                            //清楚重复
                            clearTimeout(obj.anim);
                        }
                        let id = window.setTimeout(function () {
                            //波浪移动
                            mWaveDx += mWaveSpeed;
                            if (mWaveDx >= 2 * mWaveLength) {
                                mWaveDx = 0;
                            }
                            c.clearRect(0, 0, w, h);
                            draw(obj);
                        }, 20);
                        obj.anim = id;
                    }
                }
            };
            //画文字
            function drawText(obj) {
                let X = obj.width / 2;
                let Y = obj.height / 2;
                let c = obj.ctx;
                c.fillStyle = obj.mTextColor;
                c.font = "bold " + obj.mTextSize + "px Microsoft Yahei";
                c.textBaseline = "middle";
                let info = obj.mProgress;
                // if(info>=100){
                //     info = '胜利啦'
                // }
                c.fillText(info, X - c.measureText(info).width / 2, Y, 100);
                c.fillStyle = obj.mWaveColor;
            };
            WaveCircleProgress.prototype = {
                constructor: WaveCircleProgress,
                chechkNum: function (value) {
                    if (!value) {
                        return false;
                    }
                    if (Object.prototype.toString.call(value) == "[object Number]") {
                        return true;
                    }
                    try {
                        if (parseInt(value)) {
                            return true;
                        }
                    } catch (e) {
                        console.log(e.message);
                    }
                    return false;
                }
            };
            window.WaveCircleProgress = WaveCircleProgress;
        }

    },

    beforeCreate() { },
    created() { },

    mounted() {
        this.setCircle();
            this.cricle = new WaveCircleProgress(this.circleId);
            this.cricle.setProgress(this.circleNum);
    }
};
</script>



