-->
<template>
  <div>
    <div class="konglong-content">
      <div class="progress-box">
        <div :style="{opacity: rightIndex < 100?1:0}">
          <CircleCom :circleId="'leftCircle'" :circleNum="leftIndex" :circleStyle="leftStyle"></CircleCom>
        </div>
        <div :style="{opacity: leftIndex < 100?1:0}">
          <CircleCom :circleId="'rightCircle'" :circleNum="rightIndex" :circleStyle="rightStyle"></CircleCom>
        </div>
        <!-- <div v-if="this.rightIndex < 100" class="progress-left" :class="[this.leftIndex >= 100 ? 'progress-one' : '']">
          <span :class="[leftText === true ? 'bigText' : '']">
            {{ leftIndex >= 100 ? '左边的小蓝龙胜利啦~' : leftIndex }}
          </span>
        </div>
        <div v-if="this.leftIndex < 100" class="progress-right" :class="[this.rightIndex >= 100 ? 'progress-one' : '']">
          <span :class="[rightText === true ? 'bigText' : '']"> {{ rightIndex >=      100 ? '右边的小红龙胜利啦~' : rightIndex
            }}</span> 
        </div> -->
      </div>
      <div class="konglong-box">
        <div>
          <div class="konglong-left" @keydown.space="refreshHuoqiuLeft" @click="refreshHuoqiuLeft"></div>
          <huo-qiu v-if="showHouqiu" :styleObject="'left'" :key="HouqiuLeft"></huo-qiu>
        </div>
        <div>
          <div class="konglong-right" @keydown.enter="refreshHuoqiu" @click="refreshHuoqiu"></div>
          <huo-qiu v-if="showHouqiu" :key="Houqiu"></huo-qiu>
        </div>


      </div>
    </div>
  </div>
</template>

<script>
import dayjs from 'dayjs';
import HuoQiu from './HuoQiu.vue'
import CircleCom from './Circle.vue'
export default {
  components: { HuoQiu, CircleCom },
  props: {},
  data() {
    return {
      leftIndex: 0,
      rightIndex: 0,
      showHouqiu: false,
      Houqiu: 1,
      HouqiuLeft: 1,
      leftText: false,
      rightText: false,
      leftStyle: {
        circleColor: '#65DAFF',
        textColor: '#ADEEFD'
      },
      rightStyle: {
        'circleColor': '#FBEC99',
        'textColor': '#F6B71E'
      }
    };
  },
  watch: {},
  computed: {},
  methods: {
    // 左侧
    refreshHuoqiuLeft() {
      if (this.leftIndex >= 100) {
        this.rightIndex = 0
        return
      }
      if (this.rightIndex >= 100) {
        return
      }
      this.leftText = true;
      this.showHouqiu = true;
      this.HouqiuLeft = dayjs().valueOf();
      this.leftIndex += 1;
      setTimeout(() => {
        this.leftText = false;
      }, 500)
    },
    // 右侧
    refreshHuoqiu() {
      if (this.rightIndex >= 100) {
        this.leftIndex = 0
        return
      }
      if (this.leftIndex >= 100) {
        return
      }
      this.rightText = true;
      this.showHouqiu = true;
      this.Houqiu = dayjs().valueOf();
      this.rightIndex += 1;
      setTimeout(() => {
        this.rightText = false;
      }, 500)
    },
    handleKeyDown(event) {
      if (event.key === 'Enter') {
        // 处理回车键按下事件
        this.refreshHuoqiu();
      } else if (event.key === 'Space' || event.key === " ") {
        this.refreshHuoqiuLeft();
      }
    },

  },

  beforeCreate() { },
  created() { },

  mounted() {
    window.addEventListener('keydown', this.handleKeyDown);

  }
};
</script>

<style lang="scss" scoped>
.konglong-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  //justify-content: center;
  background: url("./../assets/css/konglong/caoping_bg.jpg") no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 100vh;
  cursor: pointer;
  overflow: hidden;
}

.progress-box {
  //overflow: hidden;
  display: flex;
  height: 50px;
  width: calc(100% - 300px);
  margin: 20px 150px;
  border-radius: 30px;
  justify-content: space-between;

  .progress-left {
    width: 100px;
    height: 100px;
    background-color: #0088d7;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    font-weight: 600;
    color: #ffffff;
    // border-top-left-radius: 28px;
    // border-bottom-left-radius: 28px;
    border-radius: 50%;
  }

  .progress-right {
    width: 100px;
    height: 100px;
    background-color: chocolate;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    font-weight: 600;
    color: #ffffff;
    // border-top-right-radius: 28px;
    // border-bottom-right-radius: 28px;
    border-radius: 50%;
  }

  .progress-left span,
  .progress-right span {
    display: block;
    height: 40px;
  }

  .progress-one {
    border-radius: 28px;
  }

  .bigText {
    animation-name: animation_bigText;
    animation-duration: 0.5s;
    animation-play-state: running;
    //animation-fill-mode: forwards;
  }

  /* 动画代码*/
  @keyframes animation_bigText {
    0% {
      font-size: 30px;
      opacity: 1;
    }

    100% {
      font-size: 80px;
      //opacity: 0;
    }
  }
}

.konglong-box {
  display: flex;
  justify-content: space-between;
  height: 700px;
  width: calc(100% - 100px);
  margin: 50px;

  .konglong-left {
    background: url("./../assets/css/konglong/5.png");
    width: 600px;
    height: 700px;
    background-size: 100% 100%;
    cursor: pointer;
    transform: scaleX(-1);
    position: relative;
    filter: hue-rotate(-63deg) drop-shadow(2px 4px 6px black);
    animation: infinite;
    animation-name: animation_konglong_box;
    animation-duration: 1s;
  }

  .konglong-right {
    background: url("./../assets/css/konglong/5.png");
    width: 600px;
    height: 700px;
    background-size: 100% 100%;
    text-align: right;
    cursor: pointer;
    position: relative;
    filter: hue-rotate(111deg) drop-shadow(2px 4px 6px black);
    animation: infinite;
    animation-name: animation_konglong_box;
    animation-duration: 1s;
    animation-delay: 0.5s
  }

  @keyframes animation_konglong_box {
    0% {
      margin-top: 20px;
    }

    50% {
      margin-top: -20px;
    }

    100% {
      margin-top: 20px;
    }
  }
}
</style>
<style>
body {
  margin: 0;
  padding: 0;
}

.el-progress__text {
  padding-top: 10px !important;
  margin: 0 auto;
  display: block !important;
  text-align: center !important;
  font-size: 30px;
  font-weight: 600;
}

.el-progress-bar__outer {
  box-shadow: 0 0 2px #ffa500, 0 0 0px #ffa500, 0 0 10px #ffa500, 0 0 20px #ffa500, 0 0 10px #ff8d00;
}
</style>



