<template>
    <div :class="styleObject ==='left'?'huoqiuLeft':'huoqiu'" :style="{'margin-top':marginTop}"></div>
</template>

<script>
import dayjs from 'dayjs';
export default {
    components: {},
    props: {
        styleObject:{
            type:String,
            default:''
        }
    },
    data() {
        return {
            leftIndex: 0,
            rightIndex: 0,
            showHouqiu: true,
            Houqiu: new Date(),
            marginTop: Math.floor(Math.random() * 40)+'px'
        };
    },
    watch: {},
    computed: {},
    methods: {
        
    },

    beforeCreate() { },
    created() { },

    mounted() {
       this.marginTop = Math.floor(Math.random() * 40) + 'px'
    }
};
</script>

<style lang="scss">
.konglong-box {
    display: flex;
    justify-content: space-between;
    height: 700px;
    width: calc(100% - 100px);
    margin: 50px;
    .huoqiu {
        position: absolute;
        top: 50px;
        background: url("./../assets/css/konglong/huoqiu.png");
        width: 142px;
        height: 282px;
        transform: rotate(90deg);
        animation-name: huoqiu_animation;
        animation-duration: 2s;
        animation-fill-mode: forwards;
    }
    .huoqiuLeft{
        position: absolute;
        top: 50px;
        background: url("./../assets/css/konglong/huoqiu.png");
        width: 142px;
        height: 282px;
        transform: rotate(-90deg);
        animation-name: huoqiu_animation_left;
        animation-duration: 2s;
        animation-fill-mode: forwards;
        filter: hue-rotate(161deg);
    }

    /* 动画代码*/
    @keyframes huoqiu_animation {
        0% {
            right: 400px;
            top: 100px;
            opacity: 1;
        }

        100% {
            right: 1000px;
            top: 160px;
            opacity: 0;
        }
    }
    /* 动画代码*/
    @keyframes huoqiu_animation_left {
        0% {
            left: 400px;
            top: 100px;
            opacity: 1;
        }

        100% {
            left: 1000px;
            top: 160px;
            opacity: 0;
        }
    }
}
</style>


